import Bmusic from '@/components/bmusic.vue'
export default {
  onLoad(){
    uni.request({
      url: `http://39.107.80.8:5000/search?keywords=${this.$store.state.name}&type=1004`,
      method: 'GET',
      data: {},
      success: res => {
        const id = res.data.result.mvs[0].id
        this.$store.commit('getmvid',id)
      },
      fail: () => {},
      complete: () => {}
    });
  },
  data() {
    return {
      // 存储歌词以及时间 长度相同
      lyricObj:{
        
      },
      // 下标
      index: 0,
      //  歌词边长
      isChangelyricLong: false,
      // 初始的滚动高度
      scrollHeight: 40,
      // 控制歌词显示的距离
      padding: 60,
      // 背景图
      bgimgurl: `url(${this.$store.state.bgimgurl})`,
      // 图片的链接
      imgurl: this.$store.state.bgimgurl
    };
  },
  components: {
    Bmusic
  },
  computed: {
    // 获取背景图
    getimgbg() {
      const url = this.$store.state.bgimgurl
      return url
    },
    // 获取歌名
    getsongName(){
      return this.$store.state.name
    },
    // 获取歌手名
    getSinger(){
      return this.$store.state.singer
    },
    // 获取歌词的条数
    lyricIndex(){
      // 获取第几条歌词需要高亮
      // 当当前的播放时间大于某一项时就把下标赋值给 index （让某项的歌词高亮）
      this.$store.state.lyricObj.timeList.forEach((item,idx)=> {
        // 每次都会循环一次 当当前的进度时间到达 歌词的时间就会重新更改index
        // 让第几条歌词高亮 和滚动多少距离
        if(this.$store.state.currentTime1 > item){
          this.index = idx
        }
      })
      return this.index
    },
    // 计算属性计算是否改变歌词显示模式
    lyriclong(){
      // 点击歌词变成唱歌词模式
       if(this.isChangelyricLong){
         // 全屏歌词
         return 'lyric-scroll2'
       }else{
         // 图片下方的歌词 原样
         return ''
       }
    },
    getsongworlist(){
      // 获取歌词
      return this.$store.state.lyricObj.lyricList
    }
  },
  methods:{
    // 返回
    goBack(){
      uni.navigateBack({
          delta: 2
      });
    },
    // 进入mv播放页面
    gotomv(){
      this.$store.commit('pauseMusic')
      uni.navigateTo({
        url: '../mvinfo/mvinfo',
        // animationType: 'zoom-out',
        animationDuration: 200
      });
    },
    // toggle改变滚动条
    changelyriclong(){
      if(this.isChangelyricLong){
        this.isChangelyricLong = false
        this.padding = 60
      }else{
        this.isChangelyricLong = true
        this.padding = 165
      }
    },
    // 等图片加载完毕后再进行加载背景图防止图片是一点一点加载的
    onImageLoad(ev) {
      // 加载完成后再进行赋值
      const url = this.$store.state.bgimgurl
      // 再更改头像
      this.$store.commit('changeAvatar', url)
      // 这个是背景的更改
      this.bgimgurl = `url(${url})`
      // 这个是图片的更改
      this.imgurl = url
    }
  }
}
